<template>
  <div class="player-container">
    <Player :src="src" />
  </div>
</template>

<script>
import { Player } from "@/components";

export default {
  components: {
    Player
  },
  data() {
    return {
      // src: require("@/assets/video/08_ylxq.mp4")
      src: '',
      publishPath: process.env.BASE_URL
    };
  },
  mounted() {
    const { src } = this.$route.query;
    if (src) {
      // this.src = require("/video/"+src+".mp4");
      this.src = `/video/${src}.mp4`
    }
  },
  methods: {}
};
</script>

<style lang="less" scoped>
.player-container {
  width: 100vw;
  height: 100vh;
}
</style>
